import { Component, OnInit, Input} from "@angular/core";
import { League } from "../league";
@Component({
  selector: 'app-my-radar-chart',
  templateUrl: './my-radar-chart.component.html',
  styleUrls: ['./my-radar-chart.component.css']
})
export class MyRadarChartComponent implements OnInit {
  @Input() leagues: League[];
  public leaguesChart = [];
  public id;
  public strLeague;
  public strSport;
  public strLeagueAlternate;
  public radarChartLabels = ['Q1','Q2','Q3','Q4'];
  public radarChartData =[
    {data:[120,130,180,70],label:'2017'},
    {data:[90,150,200,45],label:'2018'}
  ]
  public radarChartType = 'radar';
  constructor() { }


  ngOnInit() {

    this.strSport = this.leagues.map(item => item.str_sport);






    function foo(arr) {
      var a = [],
        b = [],
        prev;

      arr.sort();
      for (var i = 0; i < arr.length; i++) {
        if (arr[i] !== prev) {
          a.push(arr[i]);
          b.push(1);
        } else {
          b[b.length - 1]++;
        }
        prev = arr[i];
      }

      return [a, b];
    }

    var result = foo(this.strSport);
    // console.log("[" + result[0] + "]<br>[" + result[1] + "]");

    // for (let i = 0; i < result[0].length; i++) {
    //   this.barChartsLabels.push(result[0][i]);
    // }
    // for (let i = 0; i < result[1].length; i++) {
    //   this.barChartsDataLeagues.push(result[1][i]);
    // }

  }
}

