import { Component, OnInit } from "@angular/core";

import { League } from "../league";
import { ApiService } from "../api.service";

@Component({
  selector: "app-dashboard",

  styleUrls: ["./dashboard.component.css"],
  template: `
   <div class="row my-5 ">
      <div class="col-lg-6 col-sm-12">
        <app-my-bar-chart
          [leagues]="leagues"
          *ngIf="leagues.length > 0"
        ></app-my-bar-chart>
      </div>
      <div class="col-lg-6 col-sm-12">
        <app-my-doughnut-chart [leagues]="leagues"
        *ngIf="leagues.length > 0"></app-my-doughnut-chart>
      </div>
    </div>
   <div class="row my-5 ">
      <div class="col-lg-6 col-sm-12">
        <app-my-pie-chart [leagues]="leagues"
        *ngIf="leagues.length > 0"></app-my-pie-chart>
      </div>

     <div class="col-lg-6 col-sm-12">
        <app-my-radar-chart [leagues]="leagues"
        *ngIf="leagues.length > 0"></app-my-radar-chart>
     
      </div>
  `
})
export class DashboardComponent implements OnInit {
  public leagues: League[] = [];

  selectedLeague: League = {
    id_league: null,
    str_league: null,
    str_sport: null,
    str_league_alternate: null
  };

  constructor(private apiService: ApiService) {}

  ngOnInit() {
    this.apiService.readLeagues().subscribe((leagues: League[]) => {
      this.leagues = leagues;
    });
  }
}
