import { Component, OnInit, Input} from "@angular/core";
import { League } from "../league";
@Component({
  selector: 'app-my-pie-chart',
  templateUrl: './my-pie-chart.component.html',
  styleUrls: ['./my-pie-chart.component.css']
})
export class MyPieChartComponent implements OnInit {
  @Input() leagues: League[];
  public leaguesChart = [];
  public id;
  public strLeague;
  public strSport;
  public strLeagueAlternate;
  public pieChartLabels = [];
  public pieChartData = [];
  public pieChartType = 'pie';
  constructor() { }

  ngOnInit() {

    this.strSport = this.leagues.map(item => item.str_sport);






    function foo(arr) {
      var a = [],
        b = [],
        prev;

      arr.sort();
      for (var i = 0; i < arr.length; i++) {
        if (arr[i] !== prev) {
          a.push(arr[i]);
          b.push(1);
        } else {
          b[b.length - 1]++;
        }
        prev = arr[i];
      }

      return [a, b];
    }

    var result = foo(this.strSport);
    console.log(result[0].sort())
this.pieChartLabels = result[0].sort()
this.pieChartData = result[1];
  }
}

