import { Component, OnInit, Input } from "@angular/core";

import { League } from "../league";

@Component({
  selector: "app-my-doughnut-chart",
  templateUrl: "./my-doughnut-chart.component.html",
  styleUrls: ["./my-doughnut-chart.component.css"]
})
export class MyDoughnutChartComponent implements OnInit {
  @Input() leagues: League[];

  public leaguesChart = [];
  public id;
  public strLeague;
  public strSport;
  public strLeagueAlternate;

  public doughnutChartLabels = [];
  public doughnutChartData = [120, 150, 180, 90];
  public doughnutChartType = "doughnut";
  constructor() {}

  ngOnInit() {
    this.id = this.leagues.map(item => item.id_league);
    this.strLeague = this.leagues.map(item => item.str_league);
    this.strSport = this.leagues.map(item => item.str_sport);
    this.strLeagueAlternate = this.leagues.map(
      item => item.str_league_alternate
    );

    // this.strSport.forEach(element => {

    //   this.barChartsLabels.indexOf(element) === -1 ? this.barChartsLabels.push(element) : "";

    // });

    function foo(arr) {
      var a = [],
        b = [],
        prev;

      arr.sort();
      for (var i = 0; i < arr.length; i++) {
        if (arr[i] !== prev) {
          a.push(arr[i]);
          b.push(1);
        } else {
          b[b.length - 1]++;
        }
        prev = arr[i];
      }

      return [a, b];
    }

    var result = foo(this.strSport);
    // console.log("[" + result[0] + "]<br>[" + result[1] + "]");
    console.log(result);

    function sortByFrequency(array) {
      var frequency = {};

      array.forEach(function(value) {
        frequency[value] = 0;
      });

      var uniques = array.filter(function(value) {
        return ++frequency[value] == 1;
      });

      return uniques.sort(function(a, b) {
        return frequency[b] - frequency[a];
      });
    }

    this.leaguesChart = sortByFrequency(this.strSport);

    for (let i = 1; i <= 4; i++) {
this.doughnutChartLabels.push(this.leaguesChart[i]);

    }
  }
}
